body , html {
  margin: 0;
  padding: 0;
}
table{
  border:1px solid #000;
}
td{
  width: 10px;
  height: 10px;
  border-right:1px solid #eee;
  border-bottom:1px solid #eee;
 
}
.snakeBlock{
  background-color: #90da3c !important;
  box-shadow: 1px 5px #e2e2e2;
  transition: opacity 2000ms;
  border-right:1px solid #90da3c !important;
  border-bottom:1px solid #90da3c !important;
}
.snakeHead{
  box-shadow: 1px 5px #e2e2e2;
  transition: opacity 2000ms;
  background-color: #7ec430 !important;
  border-bottom:1px solid #7ec430 !important; 
  border-right:1px solid #7ec430 !important; 
}
.snakeTail{
  background-color: #90da3c !important;
}
.FoodBlock{
  background-color: #03a9f4 !important;
  border-radius: 50%;  
}